<template>
	<div class="upload-wrap">
		<div class="form-item-title" :style="{width:labelWidth+'px'}">附件：</div>
		<div class="upload-box">
			<Upload
				ref="upload"
				:show-upload-list="false"
				:default-file-list="defaultList"
				:on-success="imgUploadSuccess"
				:format="['jpg','jpeg','png']"
				:max-size="10240"
				:on-format-error="imgFormatError"
				:on-exceeded-size="imgMaxSize"
				:before-upload="imgBeforeUpload"
				multiple
				type="drag"
				:data="upData"
				:action="`${baseInfo.baseUrl}${actionUrl}`"
				style="display:block;width:60px;height: 58px;margin-left: 10px;margin-bottom: 5px;">
				<div style="width: 58px;height:58px;line-height: 70px;">
					<Icon type="android-add" size="30" color="#afc6d5"></Icon>
				</div>
			</Upload>
			<div class="demo-upload-list" v-for="item in uploadList">
				<template v-if="item.status === 'finished'">
					<img :src="`${baseInfo.imgUrl}/${item.response?item.response:item.fileName}`" title="图片出错">
					<div class="demo-upload-list-cover">
						<Icon type="ios-eye-outline" @click.native="imgShow(item)"></Icon>
						<Icon type="ios-trash-outline" @click.native="imgRemove(item)"></Icon>
					</div>
				</template>
				<template v-else>
					<Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
				</template>
			</div>
		</div>
		<Modal title="图片查看" v-model="visible">
			<img v-if="visible" :src="`${baseInfo.imgUrl}/${imgName}`" style="width: 100%">
			<div slot="footer"></div>
		</Modal>
	</div>
</template>

<script>
	export default {
		data () {
			return {
				imgName: '',
				visible: false,
				uploadList: [],
				defaultList: this.imgList,
				baseInfo: {},
			}
		},
		props: {
			actionUrl: {
				type: String,
				required: true
			},
			upData: {
				type: Object,
				required: true
			},
			labelWidth: {
				type: Number,
				default: function () {
					return 90
				}
			},
			imgList: {
				type: Array,
				default() {
					return []
				}
			},
			maxNumber:{
				type:Number,
				default() {
					return 10
				}
			}
		},
		created: function () {
			this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
		},
		mounted () {
			this.uploadList = this.$refs.upload.fileList;
			this.upLoad(this.$refs.upload.fileList);
		},
		methods: {
			imgShow (item) {
				let name = item.response ? item.response : item.fileName;
				this.imgName = name;
				this.visible = true;
			},
			imgRemove (file) {
				let _this = this;
				let fileList = this.$refs.upload.fileList;
				let name = file.response ? file.response : file.fileName;
				_this.$http.post('imagesDo/deleteImages', {
					loanId: _this.upData.loanId,
					serialNo: _this.upData.serialNo,
					loanVoucher: name
				})
					.then(function (response) {
						let res = response.data;
						if (res.code == 1) {//成功
							_this.$Message.success(res.msg);
							_this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
							_this.upLoad(fileList)
						} else {
							_this.$Message.warning(res.msg);
						}

					})
					.catch(function (error) {
						_this.$Message.warning({
							content: error
						})
					});
			},
			imgUploadSuccess (res, file, fileList) {
				file.url = this.baseInfo.baseUrl + '/' + file.response;
				this.upLoad(fileList)
			},
			imgBeforeUpload () {
				const check = this.uploadList.length < this.maxNumber;
				if (!check) {
					this.$Notice.warning({
						title: '最多只能上传 10 张图片。'
					});
				}
				return check;
			},
			imgFormatError (file) {
				this.$Notice.warning({
					title: '文件格式不正确',
					desc: `文件${file.name}格式不正确，请上传 jpg、jpeg 或 png 格式的图片。`
				});
			},
			imgMaxSize (file) {
				this.$Notice.warning({
					title: '超出文件大小限制',
					desc: `文件${file.name}太大，不能超过 10M。`
				});
			},
			upLoad: function (fileList) {
				//编辑上传列表
				let upList = _.map(fileList, o => o.response || o.fileName);
				this.$emit('upLoad', upList)
			}
		}
	}
</script>

<style scoped>
	.upload-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		padding-bottom: 15px;
		align-items: center;
	}

	.upload-wrap .upload-box {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
		width: 630px;
	}

	.upload-wrap .demo-upload-list {
		display: block;
		width: 60px;
		height: 60px;
		text-align: center;
		line-height: 60px;
		border: 1px solid transparent;
		border-radius: 4px;
		overflow: hidden;
		background: #fff;
		position: relative;
		box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
		margin-left: 10px;
		margin-bottom: 5px;
	}

	.upload-wrap .demo-upload-list img {
		width: 100%;
		height: 100%;
	}

	.upload-wrap .demo-upload-list-cover {
		display: none;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0, 0, 0, .6);
	}

	.upload-wrap .demo-upload-list:hover .demo-upload-list-cover {
		display: block;
	}

	.upload-wrap .demo-upload-list-cover i {
		color: #fff;
		font-size: 20px;
		cursor: pointer;
		margin: 0 2px;
	}
</style>



