<template>
	<i-button type="text" @click="downLoad" :disabled="disabled">
		<slot></slot>
	</i-button>
</template>
<script>
	export default {
		data () {
			return {
				baseInfo: {},
				disabled:false,
				userInfo:{}
			}
		},
		created(){
			this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
			this.userInfo = JSON.parse(sessionStorage.getItem('userInfo'));
		},
		props: ['serialNo','type','loanId'],
		methods: {
			downLoadZip: function () {
				let _this=this;
				let form=document.createElement('form');
				let serialNoInput=document.createElement('input');
				let loanIdInput=document.createElement('input');
				let cUserIdInput=document.createElement('input');
				form.method='post';
				form.style.display='none';
				cUserIdInput.name='cUserId';
				cUserIdInput.value=_this.userInfo.userId;
				serialNoInput.name='serialNo';
				serialNoInput.value=_this.serialNo;
				loanIdInput.name='loanId';
				loanIdInput.value=_this.loanId;
				form.appendChild(cUserIdInput);
				form.appendChild(serialNoInput);
				form.appendChild(loanIdInput);
				document.body.appendChild(form);
				if(_this.type == 'annex'){
					form.action=`${_this.baseInfo.baseUrl}/imagesDo/fileToZipDown`;
				}else if(this.type == 'credit'){
					form.action=`${_this.baseInfo.baseUrl}/TempletPrint/downCreditZip`;
				}else if(this.type == 'loan'){
					form.action=`${_this.baseInfo.baseUrl}/TempletPrint/downLoanZip`;
				}else {
					_this.$Notice.warning({
						title:'下载地址错误'
					})
				}
				form.submit();
				setTimeout(function () {
					document.body.removeChild(form);
					_this.disabled=false;
				},2000)
			},
			downLoad:function () {
				this.disabled=true;
				if(this.type == 'annex'){
					this.downLoadAnnex();
				}else {
					this.downLoadZip();
				}
			},
			downLoadAnnex:function () {
				let _this = this;
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('/imagesDo/whetherZip', {
					loanId: _this.loanId,
					serialNo: _this.serialNo
				},{
					timeout:0
				})
					.then(function (response) {
						clearTimeout(timer);
						_this.$Message.destroy();
						let res=response.data;
						if(res.code == '1'){
							_this.$Message.success(res.msg);
							_this.downLoadZip();
						}else{
							_this.$Message.warning(res.msg)
							_this.disabled=false;
						}
					})
					.catch(function (error) {
						//处理错误
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.disabled=false;
						_this.$Message.error({
							content:error
						})
					});
			}
		}
	}
</script>

<style scoped>

</style>



