						   placeholder="300字以内..."></Input>
				</div>
				<up-load v-if="loanInfo.scratchFileList"
						 :actionUrl="actionUrl"
						 :upData="upData"
						 :imgList="loanInfo.scratchFileList"
						 @upLoad="handleUpLoad"></up-load>
				<div class="submit-box">
					<i-button type="info" size="large" class="sub-btn" :disabled="submitNow" @click="submit('1')">
						审核通过
					</i-button>
					<i-button type="success" size="large" class="sub-btn" :disabled="submitNow" @click="submit('3')">
						补充资料
					</i-button>
					<i-button type="error" size="large" class="sub-btn" :disabled="submitNow" @click="submit('2')">
						审核不通过
					</i-button>
