<template>
	<div class="fixed-wrap" id="fixedWrap">
		<div class="order-pic-checkbox" :class="{'scrollNow':scrollNow}">
			<Dropdown class="dropClass"  v-if="step=='loan'|| step== 'director'&& $route.params.nodeStatus >1002 && $route.params.nodeStatus !=1080 " trigger="click" @on-click="showCard">
				<i-button type="info" style="width: 120px" @click="getName('DYJFKL','DYZL')">
					抵押资料
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item name="DYSQBJJSX">抵押申请表及介绍信</Dropdown-item>
					<Dropdown-item name="DYDJSLHZD">抵押登记受理回执单</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
			<Dropdown class="dropClass"  v-if="step=='loan'|| step== 'director'&& $route.params.nodeStatus >1002 && $route.params.nodeStatus !=1080 " trigger="click" @on-click="showCard">
				<i-button type="warning" style="width: 120px" @click="getName('DYJFKL','FKSH')">
					放款审核
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item name="DBJKHT">担保借款合同</Dropdown-item>
					<Dropdown-item name="JJ">借据</Dropdown-item>
					<Dropdown-item name="YHKLS">银行卡流水</Dropdown-item>
					<Dropdown-item name="DKSPSX">贷款审批书(新)</Dropdown-item>
					<Dropdown-item name="FWZTSM">房屋状态说明</Dropdown-item>
					<Dropdown-item name="EDZYD">额度支用单</Dropdown-item>
					<Dropdown-item name="WTKKSQS">委托扣款授权书</Dropdown-item>
					<Dropdown-item name="ZXCXSYSQSFK">征信查询使用授权书（放款）</Dropdown-item>
					<Dropdown-item name="BZJZYQRH">保证金质押确认函</Dropdown-item>
					<Dropdown-item name="FKSCSPB">放款审查审批表</Dropdown-item>
					<Dropdown-item name="MQZP">面签照片</Dropdown-item>
					<Dropdown-item name="STZFSQSSKRSFZJYHK">受托支付申请书+收款人身份证及银行卡</Dropdown-item>
					<Dropdown-item name="QT">其它</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
			<Dropdown class="dropClass"  trigger="click" @on-click="showCard">
				<i-button type="info" style="width: 120px" @click="getName('SXL','SFL')">
					身份类
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item name="SFZ">身份证</Dropdown-item>
					<Dropdown-item name="HKB">户口本</Dropdown-item>
					<Dropdown-item name="HYZM">婚姻证明</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
			<Dropdown class="dropClass"  trigger="click" @on-click="showCard">
				<i-button type="warning" style="width: 120px" @click="getName('SXL','ZXL')">
					征信类
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item name="ZXBG">征信报告</Dropdown-item>
					<Dropdown-item name="GRSRZM">个人收入证明</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
			<Dropdown class="dropClass"  trigger="click" @on-click="showCard">
				<i-button type="error" style="width: 120px" @click="getName('SXL','FCL')">
					房产类
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item name="FCZHGTZBDCDJZ">房产证(含国土证)/不动产登记证</Dropdown-item>
					<Dropdown-item name="FWXXZYS">房屋信息摘要3</Dropdown-item>
					<Dropdown-item name="XHZP">下户照片</Dropdown-item>
					<Dropdown-item name="PGXX">评估信息</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
			<Dropdown class="dropClass"  trigger="click" @on-click="showCard">
				<i-button type="success" style="width: 120px" @click="getName('SXL','HTL')">
					合同类
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item name="GRDKSQSPS">个人贷款申请审批书</Dropdown-item>
					<Dropdown-item name="ZXCXSYSQSSX">征信查询使用授权书（授信）</Dropdown-item>
					<Dropdown-item name="DKYTZMWJ">贷款用途证明文件</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
			<Dropdown class="dropClass"  trigger="click" @on-click="showCard">
				<i-button type="info" style="width: 120px" @click="getName('SXL','QT')">
					其他类
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item name="KHSSHZXAJQKCL">客户涉诉或执行案件情况材料</Dropdown-item>
					<Dropdown-item name="JKRHKYHK">借款人还款银行卡</Dropdown-item>
					<Dropdown-item name="QTFYJDBCJ">其他非原件的补充件</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
			<Dropdown class="dropClass"  trigger="click">
				<i-button type="primary" style="width: 120px" >
					下载
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item>
						<down-load type="annex" :loanId="$route.params.loanId" :serialNo="serialNo">
							下载附件
						</down-load>
					</Dropdown-item>
					<Dropdown-item v-if=" step=='credit'|| step== 'director'&& $route.params.nodeStatus <= 1002 ">
						<down-load type="credit" :loanId="$route.params.loanId" :serialNo="serialNo">
							授信决议表
						</down-load>
					</Dropdown-item>
					<Dropdown-item v-if="$route.params.nodeStatus == 1081">
						<down-load type="loan" :loanId="$route.params.loanId" :serialNo="serialNo">
							放款审批表
						</down-load>
					</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
			<Dropdown v-if="step=='director' && $route.params.nodeStatus >1002 && $route.params.nodeStatus !=1080 && $route.params.tab=='pending'"
					  class="dropClass" trigger="click" @on-click="showUpload">
				<i-button type="ghost" style="width: 120px" >
					补传资料
					<Icon type="arrow-down-b"></Icon>
				</i-button>
				<Dropdown-menu slot="list" style="width: 120px">
					<Dropdown-item  v-for="item in needUpList" :key="item.thirdType" :name="item.thirdType">
						{{item.thirdTypeName}}
					</Dropdown-item>
				</Dropdown-menu>
			</Dropdown>
		</div>
	</div>
</template>
<script>
function getScroll(target, top) {
	let prop = top ? 'pageYOffset' : 'pageXOffset';
	let method = top ? 'scrollTop' : 'scrollLeft';
	let ret = target[prop];
	if (typeof ret !== 'number') {
		ret = window.document.documentElement[method];
	}
	return ret;
}
function getOffset(element) {
	let rect = element.getBoundingClientRect();
	let scrollTop = getScroll(window, true);
	let scrollLeft = getScroll(window);
	let docEl = window.document.body;
	let clientTop = docEl.clientTop || 0;
	let clientLeft = docEl.clientLeft || 0;
	return {
		top: rect.top + scrollTop - clientTop,
		left: rect.left + scrollLeft - clientLeft
	};
}
import DownLoad from '../components/download.vue'
export default {
    data () {
        return {
			scrollNow: false,
			searchParams: {
				serviceType:'SXL',
				docType: 'DYZL',
				doc: 'DYDJSQHFGJJSXYJ',
				sourceFlag:1,//默认数据来源为1：易捷；2：新网
			},
			needUpList:[]
        }
    },
	props:['step','serialNo'],
	components: {
		DownLoad
	},
	created(){
		this.loadUplist();
	},
	mounted(){
		let ob = document.getElementById('scrollWrap');
		let activeName=this.$route.params.name;
		if(activeName){
			this.$store.dispatch('changeActiveName',activeName);
		}
		ob.addEventListener('scroll', this.windowScroll, false);
	},
	destroyed(){
		let ob = document.getElementById('scrollWrap');
		ob.removeEventListener('scroll', this.windowScroll, false);
	},
	methods:{
		windowScroll: function () {
			let fixedWrap = document.getElementById('fixedWrap');
			let elOffset = getOffset(fixedWrap);
			let _this = this;
			if (elOffset.top <= 80) {
				_this.scrollNow = true;
			}
			if (elOffset.top > 80) {
				_this.scrollNow = false;
			}
		},
		getName: function (type,name) {
			this.searchParams.serviceType=type;
			this.searchParams.docType = name
		},
		showCard: function (name) {
			let _this = this;
			_this.searchParams.doc = name;
			//判断是否是属于新网数据源的图片
			let flag=_.find(_this.needUpList,o=>{return o.thirdType == name});
			if(typeof flag !== 'undefined'){
				_this.searchParams.sourceFlag=2
			}else {
				_this.searchParams.sourceFlag=1
			}
			_this.$emit('showCard',_this.searchParams);
		},
		showUpload:function (name) {
			let _this = this;
			let params=_.find(_this.needUpList,o=>{return o.thirdType == name});
			_this.$emit('showUpload',params);
		},
		loadUplist:function () {
			let _this=this;
			_this.$http.post('loanManage/listSelectAccessoryInfo',{
				status:2
			})
				.then(function (response) {
					let res=response.data;
					if(res.code == 1){
						_this.needUpList=res.data;
					}else {
						_this.$Message.warning(res.msg)
					}

				})
				.catch(function (error) {
					_this.$Message.warning({
						content:error
					})
				})
		}
	}
}
</script>

<style scoped>
	.fixed-wrap {
		height: 80px;
	}
	.order-pic-checkbox {
		background-color: #fafafa;
		max-width: 1200px;
		width: 100%;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		padding-bottom: 5px;
	}
	.order-pic-checkbox .ivu-dropdown-item {
		white-space: normal;
	}
	.order-pic-checkbox .dropClass{
		margin-right: 40px;
		margin-top: 5px;
	}
	.scrollNow {
		position: fixed;
		left: 270px;
		top: 80px;
		width: 80%;
		z-index: 10;
	}
</style>



