<template>
	<transition name="fade" mode="out-in">
		<Card class="card-wrap" ref="swiperCard" id="swiperCard">
			<p slot="title" class="imgTitle" @mousedown="dragStart($event)">
				{{imgTitle}}
			</p>
			<a href="javascript:;" class="closeBtn" @click="rotateImg" slot="extra" style="margin-right: 5px">
				<Icon type="refresh"></Icon>
				旋转
			</a>
			<a href="javascript:;" class="closeBtn" @click="closeCard" slot="extra">
				<Icon type="ios-close"></Icon>
				关闭
			</a>
			<div class="swiperBox">
				<div class="imgBox">
					<img id="activeImg" @load="initImg" @mousewheel="scaleImg" @mousedown="dragImg" :src="baseInfo.imgUrl+'/'+swiperList[activeIndex].name">
					<div class="swiper-button-prev" slot="button-prev"></div>
					<div class="swiper-button-next" slot="button-next"></div>
				</div>
				<swiper :options="swiperThumbs" ref="swiperThumbs" class="gallery-thumbs">
					<swiper-slide v-for="item in swiperList" :key="item.name">
						<div class="swiper-zoom-container">
							<img :src="baseInfo.imgUrl+'/'+item.name"  title="图片错误">
						</div>
					</swiper-slide>
				</swiper>
			</div>
		</Card>
	</transition>
</template>

<script>
	export default {
		data () {
			return {
				baseInfo: {},
				imgTitle: `${this.swiperList[0].filename}`,
				activeImg:'img0',
				rotateCount:0,
				activeIndex:0,
				imgDom:{},
				swiperThumbs: {
					notNextTick: true,
					// swiper configs
					nextButton: '.swiper-button-next',
					prevButton: '.swiper-button-prev',
					spaceBetween: 10,
					centeredSlides: true,
					slidesPerView: 'auto',
					touchRatio: 0.2,
					slideToClickedSlide: true,
					// swiper callbacks
					onSlideChangeStart:(swiper)=>{
						var _this = this;
						if (_this.swiperList.length > 0) {
							_this.imgTitle = `${_this.swiperList[swiper.activeIndex].filename}`;
							_this.activeImg = `img${swiper.activeIndex}`;
							_this.activeIndex=swiper.activeIndex;
							_this.initImg();
						};
					}
				},
				dragNow: false,
			}
		},
		props:{
			swiperList:{
				type: Array,
				default: function () {
					return []
				}
			}
		},
		watch:{
			swiperList:function (val) {
				if(val.length>0){
					this.$refs.swiperThumbs.swiper.activeIndex=0;
					this.activeIndex=0;
					this.imgTitle = val[0].filename;
				}
			}
		},
		created(){
			this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
			this.initImg();
		},
		methods: {
			dragImg:function (e) {
				e.preventDefault();
				let img=this.imgDom;
				let distanceX = e.clientX - img.offsetLeft;
				let distanceY = e.clientY - img.offsetTop;
				document.onmousemove = function (e) {
					let left = e.clientX - distanceX-24;//修复card造成的偏差
					let top = e.clientY - distanceY-67;//修复card造成的偏差
					img.style.marginLeft=left+'px';
					img.style.marginTop=top+'px';
				};
				document.onmouseup = function () {
					document.onmousemove = null;
					document.onmouseup = null;
				};
			},
			scaleImg:function (e) {//缩放
				let img=this.imgDom;
				let w=img.width;//图片的宽
				let h=img.height;//图片的高
				let ml=parseFloat(img.style.marginLeft);//左边距
				let mt=parseFloat(img.style.marginTop);//上边距
				if(h<=480 && e.deltaY>0){
					return false
				}else {
					this.imgDom.style.width=w-e.deltaY+'px';
					this.imgDom.style.height=h-e.deltaY+'px';
					this.imgDom.style.marginLeft=ml+(e.deltaY/2)+'px';
					this.imgDom.style.marginTop=mt+(e.deltaY/2)+'px';
				}
			},
			rotateImg:function () {
				this.rotateCount = (this.rotateCount+90)%360;
				this.imgDom.style.transform= `rotate(${this.rotateCount}deg)`
			},
			initOrigin:function () {
				this.imgDom.style.marginLeft=(850-this.imgDom.width)/2+'px';
				this.imgDom.style.marginTop=(480-this.imgDom.height)/2+'px';
			},
			initImg:function () {
				this.$nextTick(function () {
					let img=document.getElementById('activeImg');
					this.imgDom = img;
					img.style.height=480+'px'
					img.style.width='auto';
					img.style.transform='none'
					this.initOrigin();
				});
			},
			dragStart: function (ev) {
				let oevent = ev || event;
				let swiperCard = document.getElementById('swiperCard');
				let distanceX = oevent.clientX - swiperCard.offsetLeft;
				let distanceY = oevent.clientY - swiperCard.offsetTop;
				document.onmousemove = function (ev) {
					let oevent = ev || event;
					let left = oevent.clientX - distanceX;
					let top = oevent.clientY - distanceY;
					swiperCard.style.left = left + 'px';
					if (top < 5) {
						top = 5;
						swiperCard.style.top = top + 'px';
					} else {
						swiperCard.style.top = top + 'px';
					}

				};
				document.onmouseup = function () {
					document.onmousemove = null;
					document.onmouseup = null;
				};
			},
			closeCard: function () {
				this.$emit('close')
			}
		}
	}
</script>
<style scoped>
	.imgBox{
		width: 100%;
		height: 80%;
		overflow: hidden;
	}
	#activeImg{
		height: 100%;
		cursor: pointer;
		transform-origin: 50%;
	}
	.layout-content .card-wrap {
		width: 900px;
		height: 700px;
		position: fixed;
		left: 270px;
		top: 220px;
		z-index: 11;
	}

	#swiperCard {
		transition: none;
	}

	.imgTitle {
		text-align: center;
		cursor: move;
		-moz-user-select: none;
		-khtml-user-select: none;
		user-select: none;
		width: 100%;
		height: 100%;
	}

	.closeBtn {
		-moz-user-select: none;
		-khtml-user-select: none;
		user-select: none;
	}

	.swiperBox {
		width: 850px;
		height: 600px;
		margin: 0 auto;
	}
	.swiper-container {
		width: 100%;
		height: 300px;
		margin-left: auto;
		margin-right: auto;
	}
	.swiper-slide {
		background-size: cover;
		background-position: center;
	}

	.gallery-thumbs {
		height: 20%;
		box-sizing: border-box;
		padding: 10px 0;
	}
	.gallery-thumbs .swiper-slide {
		width: 25%;
		height: 100%;
		opacity: 0.4;
	}
	.gallery-thumbs .swiper-slide-active {
		opacity: 1;
	}
</style>



