<template>
	<div class="imgList">
		<div class="img-box">
			<div class="img-box-title">{{name}}：</div>
			<div class="demo-upload-list" v-for="item in JSON.parse(imgList)">
				<template>
					<img :src="`${baseInfo.imgUrl}/${item}`" title="图片错误">
					<div class="demo-upload-list-cover">
						<Icon type="ios-eye-outline" @click.native="handleView(item)"></Icon>
					</div>
				</template>
			</div>
		</div>
		<Modal title="查看图片" v-model="visible">
			<img :src="`${baseInfo.imgUrl}/${imgName}`" v-if="visible"
				 style="width: 100%">
			<div slot="footer"></div>
		</Modal>
	</div>
</template>

<script>
export default {
    data () {
        return {
			imgName: '',
			visible: false,
			baseInfo:{}
        }
    },
	props:{
    	name:{
    		type:String,
			default: function () {
				return '附件'
			}
		},
    	imgList:{
    		type:String,
			default: function () {
				return '[]'
			}
		}
	},
	created(){
		this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
	},
	methods:{
		handleView (name) {
			this.imgName = name;
			this.visible = true;
		}
	}
}
</script>

<style scoped>
	.img-box{
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		padding: 5px 0;
	}
	.demo-upload-list {
		display: inline-block;
		width: 60px;
		height: 60px;
		text-align: center;
		line-height: 60px;
		border: 1px solid transparent;
		border-radius: 4px;
		overflow: hidden;
		background: #fff;
		position: relative;
		box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
		margin-left: 10px;
	}

	.demo-upload-list img {
		width: 100%;
		height: 100%;
	}

	.demo-upload-list-cover {
		display: none;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0, 0, 0, .6);
	}

	.demo-upload-list:hover .demo-upload-list-cover {
		display: block;
	}

	.demo-upload-list-cover i {
		color: #fff;
		font-size: 20px;
		cursor: pointer;
		margin: 0 2px;
	}
</style>



